	data() {
		return {
			invitation: "123456789"
		}
	},
	onLoad() {

	},
	methods: {
		ctrlc: function() {
			uni.setClipboardData({
				data: this.invitation,
				success: function(data) {},
				fail: function(err) {},
				complete: function(res) {}
			})
		},
		// qrcode: function() {
		// 	uni.saveImageToPhotosAlbum({
		// 		filePath: res.tempFilePaths[0],
		// 		success: function() {
		// 			console.log('save success');
