		qrcode: function() {
			uni.downloadFile({ // 下载图片
				url: '../../../../static/imgs/heda_cs.jpg',
				success: (res) => {
					uni.saveImageToPhotosAlbum({ // 保存图片到本地
						filePath: res.tempFilePath, // 图片临时路径
						success: function() {
							uni.showToast({
								"title": "保存成功",
								duration: 2000
							})
						},
					})
				}
			})
		}
	},
}
